.pl-input {
    width: 100%;
    height: 45px;
    border: 2px solid #55D2FB;
    padding: 5px;
    padding-left: 15px;

    &::focus {
        border: 2px solid #55D2FB !important;
    }

    &--blue {
        //Placeholder color
        &::-webkit-input-placeholder { /* WebKit, Blink, Edge */
            color:    #55D2FB;
            font-weight: bold;
        }

        &:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
            color:    #55D2FB;
            font-weight: bold;
        }

        &::-moz-placeholder { /* Mozilla Firefox 19+ */
            color:    #55D2FB;
            font-weight: bold;
        }

        &:-ms-input-placeholder { /* Internet Explorer 10-11 */
            color:    #55D2FB;
            font-weight: bold;
        }
    }

    &--search {
        padding-left: 40px;
    }
}